.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card__content-wrapper {
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  align-self: center;
  width: 100%;
  max-width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin: 0;
  padding-top: 40px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__review-wrapper {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-top: 16px;
  display: flex;
}

.card__review {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-link {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: 11px 69px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
}

.card__button-link:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  transition: all .3s;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  background: linear-gradient(0deg, #f3f3f3, #f3f3f3), linear-gradient(0deg, #fff, #fff);
  margin: 0;
}

